/* Login1.css */

/* Styling for the full-page background */
.login_main {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  /* background: url("../../assets//bg2.avif") no-repeat center center fixed ; */
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("../Media/bgimage.jpg") no-repeat center center fixed;
  background-size: cover;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
}
