/* Styling for the full-page background */
/* ...existing styles... */

/* Styling for the video container */
.video-container {
  width: 100%;
}

/* Styling for the responsive video */
.responsive-video {
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  position: fixed;
  top: 0;
  left: 0;
}

/* Styling for the login container */
.login__container {
  background: rgba(255, 255, 255, 0.1);
  /* Adjust the opacity as needed */
  padding: 20px;
  border-radius: 10px;
  backdrop-filter: blur(10px);
  /* Glassmorphism effect */
  border: 1px solid rgba(255, 255, 255, 0.2);
  /* Optional border for contrast */
  text-align: center;
  max-width: 400px;
  padding-left: 70px;
  padding-right: 70px;
}

.login__container h1 {
  color: white;
  /* Text color */
  margin-bottom: 20px;
}

.login__container h5 {
  color: white;
  /* Text color */
  margin-top: 20px;
  margin-bottom: 10px;
}

.login__container input {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: none;
  border-radius: 5px;
}

.login__signInButton {
  background-color: #007bff;
  /* Button background color */
  color: white;
  /* Button text color */
  border: none;
  border-radius: 5px;
  padding: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.login__signInButton:hover {
  background-color: #0056b3;
  /* Hover state background color */
}

.login__container p {
  color: white;
  /* Text color */
  font-size: 12px;
}
