/* Styling for the full-page background */
body {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  background-size: cover;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

/* Styling for the login container */
.login__container {
  background: rgba(255, 255, 255, 0.1);
  /* Adjust the opacity as needed */
  padding: 60px;
  padding-left: 70px;
  padding-right: 70px;
  border-radius: 10px;
  backdrop-filter: blur(10px);
  /* Glassmorphism effect */
  border: 1px solid rgba(255, 255, 255, 0.2);
  /* Optional border for contrast */
  text-align: center;
  max-width: 400px;
  position: relative;
  right: 40px;
  /* Add this for absolute positioning */
}

/* Styling for the logo in the top-left corner */
.login__logo {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 200px;
  /* Adjust the width as needed */

  /* Adjust the height as needed */
}

